body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


  .signedIn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .caption {
    margin: 20px 0 40px 0;
    font-family: 'Amaranth', sans-serif;
    font-size: 18px;
    opacity: 0.8;
  }
  .skip {
    font-weight:lighter;
    color:white;
    opacity: 0.7;
    width: 100%;
    display: block;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
  }
  .skip:HOVER {
    text-decoration: underline;
  }
  .firebaseUi {
    min-width: 250px;
  }
  .button {
    cursor: pointer;
    color: blue;
    text-decoration: underline;
    margin-top: 10px;
  }

  

.firebaseui-container {
    background: rgba(0, 0, 0, 0.05);
    margin-bottom: 15px;
    min-height: 150px;
    padding-top: 10px;
    border-radius: 20px;
    box-shadow: none;
  }
  .firebaseui-container.firebaseui-page-provider-sign-in {
    background: transparent;
    box-shadow: none;
    min-height: 0;
    margin-bottom: 0;
    padding-top: 0;
  }
  .firebaseui-container.firebaseui-id-page-callback {
    background: transparent;
    box-shadow: none;
    margin-top: 40px;
    height: 84px;
    min-height: 0;
    margin-bottom: 0;
    padding-top: 0;
  }
  .firebaseui-card-header {
    display: none;
  }
  .firebaseui-subtitle, .firebaseui-text {
    color: rgba(255, 255, 255, 0.87);
  }
  .firebaseui-form-actions .mdl-button--raised.mdl-button--colored.firebaseui-button {
    background: rgba(0, 0, 0, 0.1);
  }
  .firebaseui-id-dismiss-info-bar {
    display: block;
  }
  .firebaseui-info-bar {
    border: 0;
    border-radius: 10px;
    left: 5%;
    right: 5%;
    top: 10%;
    bottom: 10%;
  }
  

