div.theoptions {
  -webkit-animation-duration: 0.75s;
          animation-duration: 0.75s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  float: left;
}

@-webkit-keyframes slidein {
  from {
    width: 0vw;  
  }
  
  to {
    width: 70vw;
  }
}

@keyframes slidein {
  from {
    width: 0vw;  
  }
  
  to {
    width: 70vw;
  }
}


